/** BANNER **/
header, .layout-main-wrapper {
  background-color: #fff; }

header .banner {
  padding: 0.7rem 0 0 0; }
  header .banner .navbar-brand {
    padding: 0;
    margin-right: 2.9rem; }

header .dfci-banner-logo .field--type-image img {
  margin-bottom: 0; }

header .search-block-form .container-inline > .container-inline {
  display: block; }

header .search-form .form-type-search {
  width: 70%; }

header .search-form .form-control {
  border-radius: 0; }

header .search-form input[type="search"] {
  border-color: #13496D;
  line-height: 1.0; }

header .search-form input[type="submit"] {
  background-color: #13496D;
  padding-top: 0;
  padding-bottom: 0;
  border: none;
  height: 30px; }

header #search-block-form {
  padding: 0; }

/** MAIN MENU **/
#navbar-main.navbar {
  border-top: #00BDFF 3px solid;
  padding-top: 0.2rem;
  padding-bottom: 0; }
  #navbar-main.navbar .hover-to-show:hover ul.dropdown-menu {
    display: block; }

.dropdown-menu {
  border-radius: 0;
  background-color: rgba(255, 255, 255, 0.95);
  border: #00BDFF 2px solid;
  margin-top: 0; }
  .dropdown-menu a {
    font-family: "realist";
    color: #13496D;
    text-decoration: none; }
  .dropdown-menu .dropdown-item a:hover,
  .dropdown-menu .dropdown-item a:focus {
    color: #00BDFF; }
  .dropdown-menu .dropdown-item.active,
  .dropdown-menu .dropdown-item:active {
    background: none; }
    .dropdown-menu .dropdown-item.active a:hover, .dropdown-menu .dropdown-item.active a:focus,
    .dropdown-menu .dropdown-item.active a.active.is-active,
    .dropdown-menu .dropdown-item:active a:hover,
    .dropdown-menu .dropdown-item:active a:focus,
    .dropdown-menu .dropdown-item:active a.active.is-active {
      color: #00BDFF; }

#navbar-main.navbar .hover-to-show:hover ul.dropdown-menu ul.dropdown-menu {
  display: none; }

#navbar-main.navbar .hover-to-show:hover ul.dropdown-menu .hover-to-show:hover ul.dropdown-menu {
  display: block;
  top: 0;
  left: 100%; }

.menu--main .nav-link {
  color: #13496D;
  font-family: "realist";
  font-weight: 400;
  text-transform: uppercase;
  font-size: 0.95rem;
  border-bottom: #fff 6px solid;
  margin: 0 0.7rem;
  padding-top: 0.1rem;
  padding-bottom: 0.2rem;
  text-decoration: none; }

.menu--main .nav-link:hover,
.menu--main .nav-link:focus,
.menu--main .nav-link.active,
.menu--main .nav-link.is-active {
