/** GROUP PAGE **/
.group-mission-statement,
.group-services,
.group-key-contact-information,
.group-project-highlight {
  margin-bottom: 2.1rem; }
  .group-mission-statement ul li,
  .group-services ul li,
  .group-key-contact-information ul li,
