  padding-bottom: 1.3rem; }
  .fast-find-list .fast-find-terms .views-row {
    margin-bottom: 0.9rem; }

/** COMMON **/
h1 {
  border-bottom: #00BDFF 3px solid;
  font-family: "rift";
  font-weight: 400;
  font-size: 2.8rem;
  display: inline-block;
  color: #13496D;
  padding: 0 2.0rem;
  line-height: 3.4rem;
  margin-bottom: -3px; }

h2 {
  color: #077ED6;
  font-family: "frank-new";
  font-weight: 400;
  font-size: 1.5rem; }

h2 + h3 {
  margin-top: 1.5rem; }

h3 {
  color: #077ED6;
  font-family: "frank-new";
  font-weight: 400;
  font-size: 1.25rem; }

a {
  color: #00BDFF;
  font-weight: 500;
